* {
    font-family: Arial, Helvetica, sans-serif;
}

body{
    padding: 60px;
}

input{
    width: 100%;
    font-size: 1.2rem;
    color: #999;
}

.card-grid{
    display: flex;
    flex-flow: row wrap;
}
.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin: 0 6px 6px 0;
}
.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    max-height: 170px;
}